import React from 'react'
import { Link } from "react-router-dom";


export default function Header() {
    return (
        <div>
            <nav className="bar">
                <ul className="navigation">
                    <li className="content" id="home">Home</li>
                    <li className="content"><Link to="/About">About us</Link></li>
                    <li className="content"><Link to="/ContactUs">Contact Us</Link></li>
                    <li className="content" id="search"> <input type="text" placeholder="Search"/></li>
                </ul>
            </nav>
        </div>
    )
}
