import React from 'react'
import { Link } from "react-router-dom";

export default function Ribbonof() {
    return (
        <div className="ribbon">
            <ol className="ollist">
            <Link to="/Computer"><li className="list">Computer</li></Link>
            <Link to="/Software"><li className="list">Software</li></Link>
            <Link to="/Hardware"><li className="list">Hardware</li></Link>
            <Link to="/Keyboard"><li className="list">Keyboard</li></Link>
            <Link to="/Mouse"><li className="list">Mouse</li></Link>
            </ol>
        </div>
    )
}
